.form {
    width: 100%;
  }
  
  .form-group {
    display: flex;
    padding: 0 15px;
    justify-content: space-between;
  }
  
  .form-input {
    border: none;
    border-bottom: 2px solid #eee;
    color: #fff;
    background: transparent;
    outline: none;
    font-size: 19px;
    width: 75%;
    padding: 5px;
  }
  
  .form-submit {
    background: #000;
    color: #fff;
    width: 18%;
    cursor: pointer;
    border: none;
    border-radius: 8px;
    outline: none;
  }
  
  .form-input::placeholder {
    color: rgb(187, 187, 187);
  }