.property {
    width: 50%;
    display: flex;
    align-items: center;
  }
  
  .property-icon {
    width: 30px;
    margin-right: 6px;
  }
  
  .property-info__value {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: #000000;
  }
  
  .property-info__description {
    font-weight: 300;
    font-size: 12px;
    line-height: 12px;
    text-transform: uppercase;
    color: #6b6b6b;
    margin-top: 3px;
  }
  
  #properties {
    position: relative;
    z-index: 1;
    padding: 18px;
    margin-top: 32px;
    display: flex;
    flex-wrap: wrap;
    row-gap: 20px;
    justify-content: space-between;
  }