.popup {
    position: fixed;
    left: 0;
    top: 0;
    width: 480px;
    transform: translateX(-200%);
    background: #8781c5;
    border-radius: 4px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease-in-out;
    z-index: 9;
  }
  
  .popup.active {
    transform: translateX(0);
  }
  
  .popup-close {
    position: absolute;
    top: 20px;
    right: 15px;
    cursor: pointer;
    width: 22px;
    height: 22px;
  }
  
  .popup-close span {
    display: block;
    margin-top: 10px;
    width: 22px;
    height: 2px;
    background: #fff;
    transform: rotate(45deg);
    position: relative;
  }
  
  .popup-close span:after {
    content: "";
    display: block;
    width: 22px;
    height: 2px;
    background: #fff;
    transform: rotate(-90deg);
    position: absolute;
    top: 0;
    left: 0;
  }